.movieItem {
  cursor: pointer;
  padding: 15px 15px 15px 15px;
  border: 3px solid #000;
  border-radius: 20px;
  width: 320px;
  transition: box-shadow 250ms linear;
}
.movieItem:hover {
  box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.5);
}
.optList {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5px;
  gap: 5px;
}
.optBtn {
  color: #807575;
  border: none;
  background-color: transparent;
  outline: none;
  display: block;
  display: flex;
  width: 30px;
  height: 30px;
}
.optIcon {
  pointer-events: none;
  margin-left: auto;
  width: 100%;
  height: 100%;
  color: inherit;
  transition: transform 250ms linear;
}
.optBtn:hover {
  color: #ff0000;
  transform: scale(1.1);
}

.title {
  margin-bottom: 10px;
}
.thumb {
  max-width: 250px;
  height: 370px;
  margin: 0 auto;
  margin-bottom: 15px;
}
.thumb > img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.date {
  margin-bottom: 10px;
}
.date,
.rating {
  display: flex;
  justify-content: flex-start;
  gap: 5px;
  align-items: center;
  font-weight: 500;
  font-size: 20px;
}

.favorite {
  color: red;
  transform: scale(1.2);
}
